"use client";

import { useEffect, useRef, useState } from "react";
import Link from "next/link";
import { motion } from "framer-motion";

type Particle = { id: number; x: number; y: number; size: number; speed: number; hue: number };

export default function ArcadePage() {
  const [mouse, setMouse] = useState({ x: 0.5, y: 0.5 });
  const [particles, setParticles] = useState<Particle[]>([]);
  const driftRef = useRef<number | null>(null);
  const canvasRef = useRef<HTMLCanvasElement | null>(null);

  useEffect(() => {
    setParticles(
      Array.from({ length: 36 }).map((_, i) => ({
        id: i,
        x: Math.random(),
        y: Math.random(),
        size: 4 + Math.random() * 8,
        speed: 4 + Math.random() * 6,
        hue: 140 + Math.random() * 160,
      }))
    );
  }, []);

  useEffect(() => {
    const onMove = (e: PointerEvent) => {
      const x = e.clientX / window.innerWidth;
      const y = e.clientY / window.innerHeight;
      setMouse({ x, y });
    };
    window.addEventListener("pointermove", onMove);
    return () => window.removeEventListener("pointermove", onMove);
  }, []);

  useEffect(() => {
    if (driftRef.current) cancelAnimationFrame(driftRef.current);
    let t = 0;
    const drift = () => {
      t += 0.002;
      if (!window.matchMedia("(pointer:fine)").matches) {
        const x = 0.5 + Math.cos(t) * 0.08;
        const y = 0.5 + Math.sin(t * 1.2) * 0.08;
        setMouse({ x, y });
      }
      driftRef.current = requestAnimationFrame(drift);
    };
    drift();
    return () => {
      if (driftRef.current) cancelAnimationFrame(driftRef.current);
    };
  }, []);

  const coreOffset = (factor: number) => ({
    x: (mouse.x - 0.5) * factor,
    y: (mouse.y - 0.5) * factor,
  });

  return (
    <div className="min-h-screen bg-black text-white">
      <div className="relative max-w-6xl mx-auto px-4 pb-16">
        <header className="flex items-center justify-between py-8">
          <div>
            <p className="text-xs uppercase tracking-[0.3em] text-green-300 mb-2">Playground</p>
            <h1 className="text-4xl md:text-5xl font-bold">Sentient Node</h1>
            <p className="text-gray-500 mt-1 max-w-xl">Interfață vie: plasmă digitală, halo de date, reacție la mișcare. Fără reguli, doar vibrație.</p>
          </div>
          <Link href="/" className="text-xs text-gray-400 hover:text-white underline">Back home</Link>
        </header>

        <motion.div
          className="relative overflow-hidden rounded-3xl border border-white/10 bg-gradient-to-br from-[#050505] via-[#0b1a1a] to-[#050505] shadow-[0_20px_120px_rgba(0,255,170,0.15)]"
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
        >
          <div className="relative h-[70vh] min-h-[480px] w-full">
            <div className="absolute inset-0 bg-[radial-gradient(circle_at_20%_20%,rgba(0,255,170,0.06),transparent_40%),radial-gradient(circle_at_80%_80%,rgba(147,51,234,0.08),transparent_40%),radial-gradient(circle_at_50%_50%,rgba(59,130,246,0.05),transparent_45%)]" />

            <motion.div
              className="absolute inset-10 rounded-[40px] border border-white/5 bg-white/5 backdrop-blur-xl overflow-hidden"
              style={{ boxShadow: "0 0 120px rgba(0,255,170,0.08)" }}
            >
              <canvas ref={canvasRef} className="hidden" />

              <div className="absolute inset-0">
                {particles.map((p) => (
                  <motion.span
                    key={p.id}
                    className="absolute rounded-full"
                    style={{
                      width: p.size,
                      height: p.size,
                      background: `radial-gradient(circle, hsla(${p.hue},80%,70%,0.9) 0%, rgba(0,0,0,0) 70%)`,
                      top: `${p.y * 100}%`,
                      left: `${p.x * 100}%`,
                      filter: "blur(0.5px)",
                    }}
                    animate={{
                      y: ["0%", "-8%", "0%"],
                      x: ["0%", "4%", "0%"],
                      opacity: [0.3, 0.8, 0.3],
                    }}
                    transition={{ duration: 6 + p.speed * 0.2, repeat: Infinity, ease: "easeInOut", delay: p.id * 0.1 }}
                  />
                ))}
              </div>

              <motion.div
                className="absolute inset-0"
                style={{
                  background: "radial-gradient(circle at 50% 50%, rgba(0,255,170,0.06), rgba(0,0,0,0))",
                }}
                animate={{ opacity: [0.4, 0.7, 0.4] }}
                transition={{ duration: 5, repeat: Infinity, ease: "easeInOut" }}
              />

              <motion.div
                className="absolute inset-0"
                style={{
                  background: `radial-gradient(circle at ${mouse.x * 100}% ${mouse.y * 100}%, rgba(59,130,246,0.12), transparent 45%)`,
                }}
              />

              <motion.div
                className="absolute left-1/2 top-1/2 h-64 w-64 md:h-80 md:w-80 rounded-[35%] bg-gradient-to-br from-emerald-300/60 via-cyan-200/50 to-purple-300/50 mix-blend-screen"
                animate={{
                  borderRadius: ["35%", "45%", "32%", "40%", "35%"],
                  rotate: [0, 6, -4, 0],
                  scale: [1, 1.05, 1.02, 1],
                }}
                transition={{ duration: 8, repeat: Infinity, ease: "easeInOut" }}
                style={{
                  x: coreOffset(30).x,
                  y: coreOffset(30).y,
                  filter: "blur(6px)",
                  transform: "translate(-50%, -50%)",
                  boxShadow: "0 0 80px rgba(0,255,170,0.35)",
                }}
              />

              <motion.div
                className="absolute left-1/2 top-1/2 h-40 w-40 md:h-56 md:w-56 rounded-full"
                animate={{ rotate: 360 }}
                transition={{ duration: 24, repeat: Infinity, ease: "linear" }}
                style={{
                  transform: "translate(-50%, -50%)",
                  background: "conic-gradient(from 0deg, rgba(0,255,170,0.3), rgba(147,51,234,0.2), rgba(59,130,246,0.25), rgba(0,255,170,0.3))",
                  mixBlendMode: "screen",
                  filter: "blur(1px)",
                  opacity: 0.8,
                }}
              />

              <motion.div
                className="absolute left-1/2 top-1/2 h-28 w-28 md:h-36 md:w-36 rounded-[28%] border border-white/30"
                animate={{
                  rotate: [-6, 6, -3, 0],
                  scale: [1, 1.08, 1],
                  opacity: [0.9, 0.6, 0.9],
                }}
                transition={{ duration: 10, repeat: Infinity, ease: "easeInOut" }}
                style={{
                  transform: "translate(-50%, -50%)",
                  boxShadow: "0 0 60px rgba(255,255,255,0.08)",
                }}
              />

              <motion.div
                className="absolute left-1/2 top-1/2 h-44 w-44 md:h-56 md:w-56 rounded-full"
                animate={{ scale: [1, 1.12, 1] }}
                transition={{ duration: 6, repeat: Infinity, ease: "easeInOut" }}
                style={{
                  transform: "translate(-50%, -50%)",
                  background: "radial-gradient(circle, rgba(255,255,255,0.08) 0%, rgba(0,0,0,0) 60%)",
                  maskImage: "radial-gradient(circle, black 60%, transparent 100%)",
                  mixBlendMode: "screen",
                }}
              />

              <motion.div
                className="absolute left-1/2 top-1/2 h-96 w-96 md:h-[28rem] md:w-[28rem] rounded-full"
                animate={{
                  scale: [1, 1.05, 0.98, 1],
                  opacity: [0.6, 0.35, 0.6],
                }}
                transition={{ duration: 12, repeat: Infinity, ease: "easeInOut" }}
                style={{
                  transform: "translate(-50%, -50%)",
                  border: "1px solid rgba(255,255,255,0.05)",
                  boxShadow: "0 0 120px rgba(59,130,246,0.15)",
                }}
              />
            </motion.div>

            <div className="absolute inset-x-10 bottom-10 flex flex-wrap items-center justify-between text-xs text-gray-500 gap-4 pointer-events-none">
              <div className="flex items-center gap-3">
                <span className="h-2 w-2 rounded-full bg-emerald-400 animate-pulse" />
                <span>Signal coherent. Entity observing.</span>
              </div>
              <div className="flex items-center gap-2 uppercase tracking-[0.3em] text-gray-600">
                <span>Hover</span>
                <span>——</span>
                <span>Pulse</span>
              </div>
            </div>
          </div>
        </motion.div>
      </div>
    </div>
  );
}
